.chakra-matrix {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 800px;
  height: 550px;
  margin-top: 70px;
  margin-bottom: 30px;
  padding: 0px;
  border-radius: 15px;
  border: 1px solid rgba(255, 255, 255, 0.33);
  box-shadow: rgba(255, 255, 255, 0.33) 0px 0px 15px;

  @media screen and (max-width: 850px) {
    flex-direction: column-reverse;
    width: 380px;
    height: 900px;
  }

  .chakra-matrix_items {
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding-left: 30px;
    @media screen and (max-width: 850px) {
      padding-left: 0px;
    }

    .chakra-matrix_item {
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 215px;
      height: 62px;
      border-radius: 8px;
      background: white;
      padding-left: 25px;


      .chakra-matrix_item_name {
        font-weight: bold;
      }

      .chakra-matrix_item_value {
        font-weight: bold;
      }

      .chakra-matrix_item_img {
        position: absolute;
        left: 171px;
        top: -1px;
        height: 62px;
      }

      .chakra-matrix_item_line {

      }
    }

    #chakra_1 {
      border: 1px solid #755AC1;

      .chakra-matrix_item_name {
        color: #755AC1;
      }

      .chakra-matrix_item_value {
        color: #755AC1;
      }
    }

    #chakra_2 {
      border: 1px solid #029AE3;

      .chakra-matrix_item_name {
        color: #029AE3;
      }

      .chakra-matrix_item_value {
        color: #029AE3;
      }
    }

    #chakra_3 {
      border: 1px solid #8FBB3F;

      .chakra-matrix_item_name {
        color: #8FBB3F;
      }

      .chakra-matrix_item_value {
        color: #8FBB3F;
      }
    }

    #chakra_4 {
      border: 1px solid #E3AE0A;

      .chakra-matrix_item_name {
        color: #E3AE0A;
      }

      .chakra-matrix_item_value {
        color: #E3AE0A;
      }
    }

    #chakra_5 {
      border: 1px solid #EE7019;

      .chakra-matrix_item_name {
        color: #EE7019;
      }

      .chakra-matrix_item_value {
        color: #EE7019;
      }
    }

    #chakra_6 {
      border: 1px solid #E92A39;

      .chakra-matrix_item_name {
        color: #E92A39;
      }

      .chakra-matrix_item_value {
        color: #E92A39;
      }
    }


  }

  video {
    border-radius: 50%;
    margin: 0 auto;
    height: 500px;
    @media screen and (max-width: 850px) {
      width: 300px;
      height: 300px;
      margin-bottom: 40px;
    }
  }
}